.link {
  text-decoration: none;
  transition: opacity 0.2s;
}
.link:hover {
  opacity: 0.8;
}

@keyframes appear {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes disappear {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.appearAnimation {
  animation: appear .5s ease-out forwards 1 running;
}

.appearAnimationDelay {
  animation: appear .5s .1s ease-out forwards 1 running;
}

.disappearAnimation {
  animation: disappear .1s ease-in forwards 1 running;
}

.disappearAnimationLong {
  animation: disappear .2s ease-in forwards 1 running;
}
.nav {
  height: 90px;
  width: 100%;
}

.nav__badge {
  margin-left: 8px;
}

.nav__badge:empty {
  display: none;
}

.nav__container {
  display: flex;
  justify-content: space-between;
  height: 100%;
  align-items: center;
}

.nav__item {
  display: flex;
  align-items: center;
  margin-right: 28px;
  height: 100%;
  color: #000000;
  position: relative;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__item_active {
  color: #4A61DD;
}

.nav__item_active:after {
  content: '';
  position: absolute;
  top: 50%;
  margin-top: 20px;
  left: 0;
  width: 100%;
  height: 2px;
  background: #4A61DD;
}

.nav__item_disabled {
  color: #8DA1B8;
  cursor: context-menu;
}

.nav__items {
  display: flex;
  height: 100%;
  flex-direction: row;
  list-style: none;
  padding: 0;
  align-items: center;
}

.nav__link {
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: inherit;
  cursor: pointer;
  font-family: 'Montserrat', sans-serif;
  transition: color .2s ease-in-out;
}

.nav__link:after {
  content: '';
  position: absolute;
  top: 50%;
  margin-top: 20px;
  left: 0;
  width: 100%;
  height: 2px;
  background: #4A61DD;
  opacity: 0;
  transition: opacity .2s ease-in-out,
              transform .2s ease-in-out;
  transform: scaleX(0);
}

.nav__link:hover {
  color: #4A61DD;
}

.nav__link:hover:after {
  opacity: 1;
  transform: scaleX(1);
}

.nav__link_active {
  color: #4A61DD;
  opacity: 1;
}

.nav__link_active:after {
  content: '';
  position: absolute;
  top: 50%;
  margin-top: 20px;
  left: 0;
  width: 100%;
  height: 2px;
  background: #4A61DD;
  opacity: 1;
  transform: scaleX(1);
}

.nav__link_disabled {
  color: #cfcfcf;
}

.nav__link_disabled:hover {
  color: #cfcfcf;
  opacity: 1;
  cursor: default;
}

.nav__link_disabled:hover:after {
  display: none;
}
