@keyframes spin {
  from {
    transform: rotate(0);
  }
  to{
    transform: rotate(359deg);
  }
}

.circle-preloader {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%)
}

.circle-preloader__body {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
}

.circle-preloader__border {
  width: 74px;
  height: 74px;
  padding: 1px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background: rgb(255, 255, 255);
  background: linear-gradient(0deg, rgba(26, 27, 34, 1) 0%, rgb(255, 255, 255) 90%);
  animation: spin .8s linear 0s infinite;
}

.circle-preloader__core {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  border-radius: 50%;
}

.circle-preloader__text {
	font-family: 'Roboto', Arial, sans-serif;
	font-size: 18px;
	line-height: 1.33;
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	margin: 24px auto 0 auto;
	color: #b6bcbf;
}


.popup {
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  display: flex;
  width: 100%;
  height: 100%;
  z-index: 4;
  top: 0;
  left: 0;
  justify-content: center;
  align-items: center;
  color: black;
  visibility: hidden;
  opacity: 0;
  transition: visibility .5s, opacity .5s linear;
}

.popup_opened {
  visibility: visible;
  opacity: 1;
}

.popup__window {
  width: 430px;
  min-width: 240px;
  background-color: #FFFFFF;
  padding: 34px 36px 32px 36px ;
  border-radius: 16px;
  box-sizing: border-box;
  position: relative;
  display: block;
  margin-right: 50px;
  margin-left: 50px;
  opacity: 0;
  transition: visibility .5s, opacity .5s linear;
}

.popup__window_opened {
  opacity: 1;
}

.popup__title {
  font-family: 'Roboto', Arial, sans-serif;
  font-weight: 900;
  font-size: 24px;
  line-height: 1.21;
  margin: 0;
  padding: 0 0 12px 0;
  display: block;
}

.popup__close {
  position: absolute;
  top: -46px;
  right: -46px;
  z-index: 2;
  width: 40px;
  height: 40px;
  background-image: url(/static/media/closeIcon.3b2d3a9a.svg);
  background-repeat: no-repeat;
  background-size: cover;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  transition: opacity 0.15s ease-in;
}

.popup__close:hover {
  opacity: .6;
}

.popup__server-error {
  font-family: 'Inter', Arial, sans-serif;
  color: #FF0000;
  font-size: 12px;
  line-height: 1.21;
  display: block;
  min-height: 15px;
  visibility: hidden;
  text-align: center;
  display: none;
}

.popup__server-error_active {
  visibility: visible;
  display: inline-block;
}



.popup__switch-container_with-form {
  font-family: 'Inter', Arial, sans-serif;
